nav.nav-wrap {
    transition: all 0.3s ease-in;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 900;
    max-width: 1000px;
    display: flex;
    justify-content: space-between;
    margin: auto;
    padding: 40px;
    @media screen and (max-width: 600px) {
        padding: 35px 20px; }
    a.nav-logo {
        height: 50px;
        svg {
            height: 100%;
            width: auto; }
        a.nav-logo.nav-dark {
            svg {
                path {
                    fill: $primary; } } } }
    ul.navigation {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        @media screen and (max-width: 600px) {
            display: none; }
        li {
            display: block;
            padding-right: 30px;
            a {
                color: $white;
                transition: all 0.15s ease-in;
                font-size: 1em; }
            a:hover {
                color: darken($white, 25%); } }
        li:last-of-type {
            padding-right: 0; } }
    ul.navigation.nav-dark {
        li {
            a {
               color: $primary; }
            a:hover {
                color: darken($white, 25%); } } } }

.nav-toggle-open-wrapper, .nav-toggle-close-wrapper {
    position: relative;
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    overflow: visible;
    svg {
        width: 24px;
        height: auto; } }
.nav-toggle-open-wrapper {
    z-index: 999;
    svg {
        stroke: $white; } }
.nav-toggle-open-wrapper.nav-dark {
    svg {
        stroke: $primary; } }

.nav-toggle-close-wrapper {
    svg {
        stroke: $primary; } }
.toggle-nav-wrapper {
    transition: all 0.2s ease-in-out;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1002;
    width: 300px;
    height: 100%;
    padding: 35px 20px 35px 0;
    background: $white;
    transform: translateX(300px);
    .internal-nav-links-wrapper, .external-links-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: right; }
    hr {
        width: 100%;
        margin: 30px 0 68px 0; }
    .internal-nav-links-wrapper {
        margin-top: 50px;
        height: 192px;
        a.internal-link-wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center; } }
    .external-links-wrapper {
        height: 122px;
        a.external-link-wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center; } }
    .external-link-icon, .internal-link-icon {
        height: 30px;
        width: 30px;
        margin-left: 20px;
        background: {
            size: contain;
            position: center center;
            repeat: no-repeat; } }
    .internal-link-icon-home {
        background-image: url('../img/icons/home.png'); }
    .internal-link-icon-blog {
        background-image: url('../img/icons/blog.png'); }
    .external-link-icon-facebook {
        background-image: url('../img/icons/fb.png'); }
    .external-link-icon-twitter {
        background-image: url('../img/icons/tw.png'); }
    .external-link-icon-instagram {
        background-image: url('../img/icons/ig.png'); }
    .nav-toggle-body-overlay {
        display: block;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1000;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.7);
        opacity: 0;
        transform: translateX(100%); }
    html.nav-open {
        body {
            overflow: hidden;
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0; }
        .nav-toggle-body-overlay {
            transition: opacity 0.2s ease-in-out, transform 0s;
            opacity: 1;
            transform: translateX(0); }
        .toggle-nav-wrapper {
            transform: translateX(0); } }
    @media screen and(min-width: 500px) {
        .nav-toggle-open-wrapper, .nav-toggle-close-wrapper, .toggle-nav-wrapper, .nav-toggle-body-overlay {
            display: none; } } }
